.btn {
  font: inherit;
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  border-radius: 4px;
  background-color: #221fec;
  color: white;
  border: 1px solid #221fec;
  margin: 0 1rem;
}

.btn:hover {
  background-color: #1752dd;
  border-color: #1752dd;
}

.btn--alt {
  background-color: transparent;
  color: #221fec;
}

.btn--alt:hover {
  background-color: #dae6f8;
}

.modal {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  background-color: white;
  padding: 1rem;
  text-align: center;
  width: 30rem;
  z-index: 10;
  position: fixed;
  top: 20vh;
  left: calc(50% - 15rem);
}

.backdrop {
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.mdl-p {
  font-size: 30px;
  color: black;
}
